
import { getBannerList } from '@/api/home.js'
import { getRecommendList } from '@/api/detail.js'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import XTabbar from '@/components/tabbar.vue'
import tags from './components/tags'
import CountDown from 'vue2-countdown'
export default {
name: 'homeIndex',
components: {
Group,
Cell,
Swiper,
tags,
Datetime,
Popup,
XTabbar,
CountDown
},
data () {
return {
isLogin: false,
visible: false,
top: 0,
left: 0,
selectedTag: {},
date: '2018-08-17',
show: false,
indexCate: [],
bannerlist: [],

recommendskulist: [],
indexact: [],
indexactmain: {starttime: '2018-08-17', stoptime: '2018-08-17'},
indexactchild: [],
searchword:''
}
},
methods: {
routerGo (url) {

location.href = url
},
activetime (row) {
if (row == undefined) { return }
var t = new Date(row.replace(/-/g, '/'))
t = Date.parse(t)
console.log(t)
return t
},
nowdate () {
var date = new Date() 
var str = date.getTime()
return str
},
getRecommend () {
const that = this
const postData = {}
getRecommendList(postData)
.then(res => {
if (res.data.errorcode === 0) {
this.recommendskulist = res.data.data.recommendskulist
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('', 'middle')
})
},
actindex () {
const postDatab = {
location: 'indexact'
}
getBannerList(postDatab)
.then(res => {
if (res.data.errorcode === 0) {
this.indexactmain = res.data
this.indexact = res.data.data.bannerlist
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('', 'middle')
})
}
},
created () {

const postDataCate = {
location: 'indexmode'
}
getBannerList(postDataCate)
.then(res => {
if (res.data.errorcode === 0) {
this.indexCate = res.data.data.bannerlist
this.searchword = res.data.data.searchword
window.localStorage.setItem('searchword', this.searchword)
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('', 'middle')
})

this.isLogin = localStorage.getItem('accountIsLogin')
const that = this
const postData = {
location: 'index'
}
getBannerList(postData)
.then(res => {
if (res.data.errorcode === 0) {

var list = []
res.data.data.bannerlist.map(item => {
let listurl = {}
listurl.img = item.picurl
listurl.url = item.target
listurl.title = item.target
list.push(listurl)
})
that.bannerlist = list
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('', 'middle')
})
const postDatac = {
location: 'indexactchild'
}
getBannerList(postDatac)
.then(res => {
if (res.data.errorcode === 0) {

that.indexactchild = res.data.data.bannerlist
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.toast.text('', 'middle')
})
this.actindex()
this.getRecommend()
}
}



